import React, { useState, useEffect } from "react";
// import axios from "axios";
// import { useCookies } from "react-cookie";
import { useNavigate, Link, Navigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import "./login.scss";
import { supabase } from "../supabase";

export const LogIn = () => {
  const [session, setSession] = useState(null);

  useEffect(() => {
    setSession(supabase.auth.getSession());
    supabase.auth.onAuthStateChange((event, session) => {
      setSession(session);
    });
  }, []);

  //
  const nav = useNavigate();

  const auth = supabase.auth.getSession();

  //
  // const [, setCookie] = useCookies();
  // const [, setToken] = useState();
  //
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();

  //
  const [mail, setMail] = useState("");
  const [password, setPassword] = useState("");

  //
  const [logError, setLogError] = useState();

  const handleMailChange = (e) => setMail(e.target.value);
  const handlePasswordChange = (e) => setPassword(e.target.value);

  const onLogIn = () => {
    reset();

    //API
    const data = {
      email: mail,
      password: password,
    };

    //API
  };
  // if (auth) return <Navigate to="/dieter" />;

  return (
    <div>
      <main className="login">
        <h2></h2>
        <p id="error">{logError}</p>

        <form onSubmit={handleSubmit(onLogIn)} className="login-Form">
          <label></label>
          <br />
          <input
            {...register("mail", {
              pattern: {
                value: /\S+@\S+\.\S+/,
                message: "",
              },
            })}
            id="login-Form__email-Set"
            type="text"
            onChange={(e) => handleMailChange(e)}
            aria-label="mailLog"
          />
          <div id="login-Form__errorM">
            {errors.mail && <span>{errors.mail.message}</span>}
          </div>
          <br />
          <br />
          <label></label>
          <br />
          <input
            {...register("password", {
              minLength: {
                value: 8,
                message: "824",
              },
              maxLength: {
                value: 24,
                message: "824",
              },
              pattern: {
                value: /^[a-zA-Z0-9.?/-]/,
                message: "",
              },
            })}
            id="login-Form__password-Set"
            type="text"
            onChange={(e) => handlePasswordChange(e)}
            aria-label="passLog"
          />
          <div id="login-Form__errorP">
            {errors.password && <span>{errors.password.message}</span>}
          </div>
          <br />
          <br />
          <input type="submit" id="login-Form__login-Button" value="" />
        </form>

        <br />
        <Link to="/dieter/SignUp"></Link>
      </main>
    </div>
  );
};

export default LogIn;
