import axios from "axios";
import React, { useState } from "react";
import { useNavigate, Navigate, Route } from "react-router-dom";
import { Link } from "react-router-dom";
import { useForm } from "react-hook-form";
import Compressor from "compressorjs";
import "./signup.scss";
import { supabase } from "../supabase.js";

export const SignUp = () => {
  //
  const nav = useNavigate();

  //
  const auth = supabase.auth.getSession(); //multipart/form-data
  // const icon = new FormData();

  //
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();

  //
  const [mail, setMail] = useState("");
  const [name, setName] = useState("");
  const [password, setPassword] = useState("");

  //
  const [signError, setSignError] = useState("");

  //
  const handleMailChange = (e) => setMail(e.target.value);
  const handleNameChange = (e) => setName(e.target.value);
  const handlePasswordChange = (e) => setPassword(e.target.value);

  //API
  // const handleIconSet = (e) => {
  //   const data = e.target.files[0];

  //   new Compressor(data, {
  //     quality: 0.6,
  //     success(result) {
  //       icon.append("icon", result);
  //     },
  //     error(err) {
  //       console.log("" + err);
  //     },
  //   });
  // };

  //
  const onSignUp = async () => {
    reset();

    //API
    // const data = {
    //   email: mail,
    //   name: name,
    //   password: password,
    // };

    await supabase.auth.signUp({
      email: mail,
      password: password,
    });

    //API

    //
    if (auth) return <Route element={<Navigate to="/dieter" />}></Route>;
  };

  return (
    <div>
      <main className="main">
        <h2></h2>
        <p className="error-Mes">{signError}</p>

        <form onSubmit={handleSubmit(onSignUp)} className="signup-Form">
          <label></label>
          <br />
          <input
            {...register("mail", {
              required: "",
              pattern: {
                value: /\S+@\S+\.\S+/,
                message: "",
              },
            })}
            className="signup-Form__mail-log"
            type="text"
            onChange={(e) => handleMailChange(e)}
          />
          <div id="signup-Form__error_mail">
            {errors.mail && <span>{errors.mail.message}</span>}
          </div>
          <br />

          {/* <br />
          <label></label>
          <br />
          <input
            {...register("name", {
              required: "",
              minLength: { value: 2, message: "2" },
            })}
            className="signup-Form__user-Set"
            type="text"
            onChange={(e) => handleNameChange(e)}
            id="signup-Form__name-Set"
          />
          <div id="signup-Form__error_name">
            {errors.name && <span>{errors.name.message}</span>}
          </div>
          <br /> */}

          <br />
          <label></label>
          <br />

          <input
            {...register("password", {
              required: "",
              minLength: {
                value: 8,
                message: "824",
              },
              maxLength: {
                value: 24,
                message: "824",
              },
              pattern: {
                value: /^[a-zA-Z0-9.?/-]/,
                message: "",
              },
            })}
            className="signup-Form__pass-Set"
            type="password"
            onChange={(e) => handlePasswordChange(e)}
          />
          <div id="signup-Form__error_pass">
            {errors.password && <span>{errors.password.message}</span>}
          </div>
          <br />

          {/* <br />
          <label className="signup-Form__icon"></label>
          <br />

          <input
            className="signup-Form__icon-Set"
            type="file"
            id="icon"
            accept=".jpg,.png"
            onChange={(e) => handleIconSet(e)}
          />
          <br /> */}

          <br />
          <input
            type="submit"
            value=""
            id="signup-Form__sign-Button"
          ></input>
        </form>
        <br />
        <Link to="/dieter/Login" id="link-Log">
          
        </Link>
      </main>
    </div>
  );
};

export default SignUp;
